<template>
  <div class="bc-outline-main-center">
    <h3>文章列表:</h3>
    <ul class="outline-main-center-article-list">
      <li 
        v-show="tableData.length == 0 ? true : false"
        class="outline-main-center-article-list-li-frist"
        >
        目前没有文章 快去<a href="#/publish">发表文章</a>
      </li>
      <li 
      v-for="item in tableData"
      :key="item.id"
      >
      <span @click="handleClick(item)" class="outline-main-center-article-list-title">{{item.title}}</span>
      <span class="outline-main-center-article-list-time">{{item.pub_date}}</span>
      <span v-if="item.new" class="outline-main-center-article-list-new">new</span>
      <span v-if="item.read_num>=500" class="outline-main-center-article-list-hot"><i class="icon">&#xe6a0;</i></span>
      </li>
    </ul>
    <div class="block">
      <el-pagination
        layout="prev, pager, next"
        :total="pageNum"
        :page-size='20'
        @current-change='currentchange'
        >
      </el-pagination>
    </div>
    <outilineCenterTask></outilineCenterTask>
  </div>
</template>

<script>
import * as ajax from '@/api'
import outilineCenterTask from '@/components/Outline/outilineCenterTask'

export default {
  data() {
    return {
      html: [],
      tableData: [],
      data: "",
      pageNum:1
    };
  },
  methods: {
    handleClick(row) {
      this.$router.push({path:'/article', query:{row:JSON.stringify(row)}}) //查看文章
    },
    currentchange(page){
      ajax.getOutlineListPage(this.$route.query.id,page).then(resp=>{
        console.log(resp)
      })
    }
  },
  props: ["outlineList"],
  watch: {
    arcitle(newval, old) {
      this.html = newval;
    }
  },
  created() {
    this.bus.$on('toggleArictleList',id=>{
      ajax.getOutlineList(id).then(resp=>{
        var date = new Date()
        var year = date.getFullYear(),mouth = date.getMonth()+1,day = date.getDate() //当前时间
        if(resp.data.code){
          resp.data.data.article_info.forEach(element => {
            let dated = element.pub_date.split(' ')[0].split('-')
            if(year-Number(dated[0])==0){
              if(mouth-Number(dated[1])==0){
                if(day-Number(dated[2])<=7){
                  element['new'] = true
                }
              }else{
                  element['new'] = false
              }
            }else{
              element['new'] = false
            }
          })
          this.pageNum = resp.data.data.page_info.totalPage*20
          this.tableData = resp.data.data.article_info
          
        }else{
          this.tableData = []
        }
      })
    })
    ajax.getOutlineList(this.$route.query.id).then(resp=>{
      console.log(resp)
        if(resp.data.code){
          var date = new Date()
          var year = date.getFullYear(),mouth = date.getMonth()+1,day = date.getDate() //当前时间
          resp.data.data.article_info.forEach(element => {
            let dated = element.pub_date.split(' ')[0].split('-')
            if(year-Number(dated[0])==0){
              if(mouth-Number(dated[1])==0){
                if(day-Number(dated[2])<=7){
                  element['new'] = true
                }
              }else{
                  element['new'] = false
              }
            }else{
              element['new'] = false
            }
          
        })
        this.pageNum = resp.data.data.page_info.totalPage*20
        this.tableData = resp.data.data.article_info || []
      }
    })
  },
  components:{
    outilineCenterTask
  }

};
</script>

<style lang='scss'>
@font-face {
  font-family: 'iconfont';  /* project id 1338628 */
  src: url('//at.alicdn.com/t/font_1338628_2kayeuh5fie.eot');
  src: url('//at.alicdn.com/t/font_1338628_2kayeuh5fie.eot?#iefix') format('embedded-opentype'),
  url('//at.alicdn.com/t/font_1338628_2kayeuh5fie.woff2') format('woff2'),
  url('//at.alicdn.com/t/font_1338628_2kayeuh5fie.woff') format('woff'),
  url('//at.alicdn.com/t/font_1338628_2kayeuh5fie.ttf') format('truetype'),
  url('//at.alicdn.com/t/font_1338628_2kayeuh5fie.svg#iconfont') format('svg');
}
.icon{
   font-family: 'iconfont';
}
.bc-outline-main-center {
  flex: 1;
  min-width: 730px;
  margin-left: 20px;
  height: auto;
    .outline-main-center-article-list{
      list-style: none;
      .outline-main-center-article-list-li-frist{
        a{
          color: rgb(30, 30, 240);
        }
      }
      li{
        height: 50px;
        font-size: 15px;
        line-height: 50px;
        border-bottom: 1px solid #f6f6f6;
        .outline-main-center-article-list-title{
          cursor:pointer
        }
        .outline-main-center-article-list-time{
          float: right;
          font-size: 12px;
          color: #999;
        }
        .outline-main-center-article-list-new{
          background-color: rgb(89, 149, 228);
          color: #fff;
          font-size: 12px;
          border-radius: 4px;
          float: right;
          margin:18px 10px 0 0;
          height: 15px;
          line-height: 15px;
          width: 26px;
          text-align: center;
        }
        .outline-main-center-article-list-hot{
          color: red;
          font-size: 20px;
          border-radius: 4px;
          float: right;
          margin:18px 10px 0 0;
          height: 15px;
          line-height: 15px;
          width: 26px;
          text-align: center;
        }
    }
  }

  .editor-container {
    min-height: 400px;
    position: relative;
    h1 {
      position: absolute;
      padding-left: 13px;
    }
    .simplemde-container {
      // height: auto !important;
      min-height: 405px;
      width: 100%;
      #contentEditor {
        display: none;
      }
    }
  }

  .block{
      text-align: center;
    }
}
</style>
