<template>
  <div class="wiki-outline-main-left">
    <div class="wiki-outline-main-left-main">
      <el-tree
        :data="data"
        :props="defaultProps"
        accordion
        @node-click="handleNodeClick"
        :default-expanded-keys="defaultArr"
        node-key='id'
        :highlight-current='true'
        >
      </el-tree>
    </div>
  </div>
</template>

<script>
import * as ajax from "@/api";
import { mapMutations } from "vuex";

export default {
  data() {
    return {
      title: "",
      defaultArr:[],
      data: [],
      defaultProps: {
        children: "children",
        label: "label",
      }
    };
  },
  props: ["outlineList", "default"],
  methods: {
    toggleAritcle(id, index, item) {
      this.$emit("getAricitleTitle", item.title);
      ajax.getArticleDetail(id).then(resp => {
        this.getarticle(resp.data.data.article_info.content); //获取文章内容
      });
    },
    ...mapMutations(["getarticle"]),
    filterNode(value, data) {
      if (!value) return true;
      return data.label.indexOf(value) !== -1;
    },
    handleNodeClick(filterNode,node){
      if(filterNode.book_name){
        this.bus.$emit('toggleArictleList',filterNode.id)
        this.$router.push({path:'/outline', query:{id:node.data.id, type_name:node.parent.label, book_name:node.data.book_name, type_id:node.data.type_id}}) //切换路由
        this.$emit('toggleNav',{id:node.data.id, type_name:node.parent.label, book_name:node.data.book_name, type_id:node.data.type_id+1}) //切换面包屑
      }else{
        this.$route.query.type_id = filterNode.type.id
      }
    }

  },
  created() {
    this.title = this.$route.query.book_name;
    ajax.getHomeList().then(resp=>{
      if(resp.data.code){
        this.data2 = resp.data.data.all_type_books
        let data = resp.data.data.all_type_books
        data.forEach(element => {
          element["value"] = element["type"]["id"];
          element["label"] = element["type"]["type_name"];
          element["children"] = element["books"];
          element["children"].forEach(item => {
            item["value"] = item["id"];
            item["label"] = item["book_name"];
          });
        });
        this.data = data
        this.defaultArr = [Number(this.$route.query.type_id)+1] //默认展开
      }
    })
  },
  watch: {
    filterText(val) {
      this.$refs.tree2.filter(val);
    }
  }
};
</script>

<style lang='scss'>
.wiki-outline-main-left {
  width: 200px;
  min-height: 100%;
  .is-expanded{
    
  }
}
</style>
